body, .community-container {
  background: linear-gradient(135deg, #39836e 0%, #24664a 100%);
  min-height: 100vh;
}

.community-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px 20px 60px 20px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.community-header {
  background: #1a2233;
  border-radius: 24px;
  padding: 32px;
  color: #fff;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  text-align: center;
}

.community-header h1 {
  color: #fbbf24;
  margin: 16px 0 24px 0;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.community-nav {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}

.nav-tab {
  background: #232e41;
  color: #fff;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
}

.nav-tab:hover {
  background: #28344a;
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(44,62,80,0.15);
}

.nav-tab.active {
  background: #fbbf24;
  color: #fff;
  box-shadow: 0 4px 16px rgba(251,191,36,0.3);
}

.community-content {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.leaderboard-section, .discussions-section {
  background: #1a2233;
  border-radius: 24px;
  padding: 32px;
  color: #fff;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
}

.leaderboard, .thread-list, .thread-page {
  background: transparent;
  border-radius: 0;
  padding: 0;
  color: #fff;
  box-shadow: none;
  margin-bottom: 0;
}

.leaderboard h2 {
  color: #fbbf24;
  margin-bottom: 24px;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.leaderboard ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.leaderboard li {
  display: flex;
  align-items: center;
  gap: 12px;
  background: #202b3a;
  border-radius: 12px;
  margin-bottom: 10px;
  padding: 14px 20px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: background 0.2s;
}

.leaderboard .self {
  background: #223355;
  font-weight: bold;
  border: 2px solid #fbbf24;
}

.leaderboard .rank {
  color: #fbbf24;
  font-weight: bold;
  font-size: 1.2rem;
  margin-right: 8px;
}

.leaderboard .streak {
  margin-left: auto;
  color: #fbbf24;
  font-weight: bold;
}

.leaderboard .you {
  color: #fff;
  background: #fbbf24;
  border-radius: 8px;
  padding: 2px 10px;
  margin-left: 8px;
  font-size: 1rem;
}

.leaderboard .your-rank {
  margin-top: 18px;
  color: #fbbf24;
  font-weight: bold;
  font-size: 1.1rem;
}

.thread-list-header {
  display: flex;
  gap: 16px;
  margin-bottom: 18px;
}

.thread-list-header input[type="text"] {
  flex: 1;
  padding: 14px 18px;
  border-radius: 10px;
  border: none;
  background: #232e41;
  color: #fff;
  font-size: 1rem;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
  transition: box-shadow 0.2s, background 0.2s;
}

.thread-list-header input[type="text"]:focus {
  outline: none;
  background: #28344a;
  box-shadow: 0 0 0 2px #fbbf24;
}

.thread-list-header button, .feature-button, .modal button, .comments button, .back-button {
  background: #fbbf24;
  color: #222;
  border: none;
  padding: 14px 28px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 0;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
}

.thread-list-header button:hover, .feature-button:hover, .modal button:hover, .comments button:hover, .back-button:hover {
  background: #f59e0b;
}

.thread-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.thread-list li {
  background: #232e41;
  border-radius: 10px;
  margin-bottom: 14px;
  padding: 18px 20px;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
}

.thread-list li:hover {
  background: #28344a;
}

.thread-title {
  font-weight: bold;
  color: #fff;
  font-size: 1.15rem;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.ayah-share-badge {
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  color: #222;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 4px rgba(251, 191, 36, 0.3);
}

.thread-meta {
  color: #a0aec0;
  font-size: 0.95em;
  display: flex;
  gap: 18px;
  margin-top: 2px;
}

.modal {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.7);
  display: flex; align-items: center; justify-content: center;
  z-index: 1000;
}

.modal form {
  background: #1a2233;
  color: #fff;
  padding: 32px 28px;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  min-width: 340px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
}

.modal input[type="text"], .modal textarea {
  background: #232e41;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 1rem;
  resize: none;
}

.modal input[type="text"]:focus, .modal textarea:focus {
  outline: none;
  background: #28344a;
  box-shadow: 0 0 0 2px #fbbf24;
}

.thread-page {
  margin-top: 0;
}

.back-button {
  margin-bottom: 18px;
  background: #fbbf24;
  color: #222;
  border: none;
  padding: 10px 22px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
  text-decoration: none;
  display: inline-block;
}

.thread-content {
  margin: 18px 0 24px 0;
  font-size: 1.1rem;
  color: #e0e6ed;
}

/* Ayah Share Formatting */
.ayah-share-section {
  background: linear-gradient(135deg, #39836e 0%, #24664a 100%);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  color: white;
}

.ayah-share-title {
  color: #fbbf24;
  font-size: 1.2rem;
  margin: 0 0 16px 0;
  font-weight: 700;
}

.ayah-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.ayah-line {
  font-size: 1.1rem;
  line-height: 1.6;
  background: rgba(255, 255, 255, 0.1);
  padding: 12px;
  border-radius: 8px;
  border-left: 4px solid #fbbf24;
}

.translation-section {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.translation-title {
  color: #fbbf24;
  font-size: 1.1rem;
  margin: 0 0 12px 0;
  font-weight: 600;
}

.translation-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.translation-line {
  font-size: 1rem;
  line-height: 1.5;
  color: #e0e6ed;
}

.thoughts-section {
  background: rgba(251, 191, 36, 0.1);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid rgba(251, 191, 36, 0.2);
}

.thoughts-title {
  color: #fbbf24;
  font-size: 1.1rem;
  margin: 0 0 12px 0;
  font-weight: 600;
}

.thoughts-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.thoughts-line {
  font-size: 1rem;
  line-height: 1.5;
  color: #e0e6ed;
}

.regular-content {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #e0e6ed;
  margin-bottom: 16px;
}

.comments {
  margin-top: 24px;
}

.comments form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 18px;
}

.comments textarea {
  background: #232e41;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 1rem;
  resize: none;
  min-height: 60px;
}

.comments textarea:focus {
  outline: none;
  background: #28344a;
  box-shadow: 0 0 0 2px #fbbf24;
}

.comments button {
  align-self: flex-end;
}

.comments ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.comments li {
  background: #232e41;
  border-radius: 8px;
  margin-bottom: 12px;
  padding: 14px 16px;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
}

.comment-user {
  font-weight: bold;
  color: #fbbf24;
  margin-right: 10px;
}

.comment-date {
  font-size: 0.9em;
  color: #a0aec0;
  margin-left: 10px;
}

.like-button {
  background: #fbbf24;
  color: #222;
  border: none;
  padding: 8px 18px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 0;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
  display: inline-block;
}

.like-button:hover {
  background: #f59e0b;
}

/* Responsive Design */
@media (max-width: 768px) {
  .community-container {
    padding: 20px 16px 40px 16px;
  }
  
  .community-header {
    padding: 24px 20px;
  }
  
  .community-header h1 {
    font-size: 2rem;
  }
  
  .community-nav {
    flex-direction: column;
    gap: 12px;
  }
  
  .nav-tab {
    padding: 14px 24px;
    font-size: 1rem;
  }
  
  .leaderboard-section, .discussions-section {
    padding: 24px 20px;
  }
  
  .thread-list-header {
    flex-direction: column;
    gap: 12px;
  }
} 